import { AuthProvider } from '../types/provider.interface';

const USERNAME = 'username';

const authProvider: AuthProvider = {
  // call when the user attempts to log in
  login: ({ username }) => {
    localStorage.setItem(USERNAME, username);
    // accept all username/password combinations
    return Promise.resolve();
  },
  // called when the user clicks on the logout button
  logout: () => {
    localStorage.removeItem(USERNAME);
    return Promise.resolve();
  },
  // called when the API returns an error
  checkError: ({ status }) => {
    if (status === 401 || status === 403) {
      localStorage.removeItem(USERNAME);
      return Promise.reject();
    }
    return Promise.resolve();
  },
  // called when the user navigates to a new location, to check for authentication
  checkAuth: () => {
    return localStorage.getItem(USERNAME)
      ? Promise.resolve()
      : Promise.reject();
  },
  // called when the user navigates to a new location, to check for permissions / roles
  getPermissions: () => Promise.resolve(),
  getIdentity: async () => {
    let fullName = 'Jane Doe';
    let avatar =
      'data:image/jpeg;base64,';

    try {
      const response = await fetch('https://randomuser.me/api/');
      const data = await response.json();
      fullName = data.results[0].name.first + ' ' + data.results[0].name.last;
      avatar = data.results[0].picture.thumbnail;
    } catch (error) {
      console.log(error);
    }
    return Promise.resolve({ id: 'user', fullName, avatar });
  },
};

export default authProvider;
